import React from 'react';
import { useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import "swiper/swiper.min.css";
import "swiper/components/navigation/navigation.min.css"

import {
  Box,
  Center,
  Text,
  Flex,
  keyframes,
  usePrefersReducedMotion,
} from '@chakra-ui/react';

import static01 from '../assets/images/static.gif';
import feature01 from '../assets/images/feature_01.jpg';
import crossPattern from '../assets/images/crossPattern.png';

import squat01 from '../assets/images/squat01.jpg';
import squat02 from '../assets/images/squat02.jpg';
import squat03 from '../assets/images/squat03.jpg';
import squat04 from '../assets/images/squat04.jpg';
import squat05 from '../assets/images/squat05.jpg';
import squat06 from '../assets/images/squat06.jpg';

import report01 from '../assets/images/report01.jpg';
import report02 from '../assets/images/report02.jpg';

const bgAnimate = keyframes`
   0% { background-image: url('${squat01}'); }
   16% { background-image: url('${squat02}'); }
   32% { background-image: url('${squat03}'); }
   48% { background-image: url('${squat04}'); }
   65% { background-image: url('${squat05}'); }
  83% { background-image: url('${squat06}'); }
  100% { background-image: url('${squat01}'); }
`;

const reportAnimate = keyframes`
  0% { background-image: url('${report01}'); }
  50% { background-image: url('${report02}'); }
  100% { background-image: url('${report01}'); }
`;

// SwiperCore.use([Navigation, Pagination, Scrollbar, A11y]);

const FeatureSection = () => {

  const prefersReducedMotion = usePrefersReducedMotion();

  const animation = prefersReducedMotion
    ? undefined
    : `${bgAnimate} infinite 20s step-start`;

  const reportAnimation = prefersReducedMotion
    ? undefined
    : `${reportAnimate} infinite 1.5s step-start`;

  const [mySwiper, setMySwiper] = useState(null);

  // const slideTo01 = () => {
  //   mySwiper.slideTo(0);
  //   console.log(mySwiper.activeIndex)
  // };
  // const slideTo02 = () => {
  //   mySwiper.slideTo(1);
  // };
  // const slideTo03 = () => {
  //   mySwiper.slideTo(2);
  // };

  const indexNumer = 0;
  // const defaultIndex = '0';
  const [swiperIndex, setSwiperIndex] = useState(indexNumer);

  const handleClick = (index) => (e) => {
    console.log(e.target);
    if (index === 0) {
      setSwiperIndex(0);
      mySwiper.slideTo(0);
      console.log(mySwiper.activeIndex);
    } else if (index === 1) {
      setSwiperIndex(1)
      mySwiper.slideTo(1);
      console.log(mySwiper.activeIndex);
    } else if (index === 2) {
      setSwiperIndex(2)
      mySwiper.slideTo(2);
      console.log(mySwiper.activeIndex);
    }
  };

  const handleSwipe = () => {
    console.log(mySwiper.activeIndex)
    const index = mySwiper.activeIndex
    if (index === 0) {
      setSwiperIndex(0);
      console.log(mySwiper.activeIndex);
    } else if (index === 1) {
      setSwiperIndex(1)
      console.log(mySwiper.activeIndex);
    } else if (index === 2) {
      setSwiperIndex(2)
      console.log(mySwiper.activeIndex);
    }

  }

  const index0 = swiperIndex === 0;
  const index1 = swiperIndex === 1;
  const index2 = swiperIndex === 2;

  return (
    <Box
      my="100px"
      py="60px"
      bgImage={`url('${crossPattern}')`}
      bgSize="30px 30px"
      mx="24px"
    >
      <Flex direction="column" align="center" mb="20px">
        <Text color="blue.400">3 分鐘 提供 3 大特色 </Text>
        {/* <Heading as="h2" my=".5em" size="lg">AI 體態評估</Heading> */}
      </Flex>

      <Flex justify="center " mb="20px">
        <Flex
          onClick={handleClick(0)}
          direction="column"
          align="center"
          w={{ base: "100px", md: "180px" }}
          bg={index0 ? "#046EB0" : "#272728"}
          borderRadius="6px"
          py="6px"
          mx="10px"
        >
          <Text fontSize="8px">Point</Text>
          <Text fontSize="16px">01</Text>
          <Text fontSize="18px">靜態評估</Text>
        </Flex>
        <Flex
          onClick={handleClick(1)}
          direction="column"
          align="center"
          w={{ base: "100px", md: "180px" }}
          bg={index1 ? "#046EB0" : "#272728"}
          borderRadius="6px"
          py="6px"
          mx="10px"
        >
          <Text fontSize="8px">Point</Text>
          <Text fontSize="16px">02</Text>
          <Text fontSize="18px">動態評估</Text>
        </Flex>
        <Flex
          onClick={handleClick(2)}
          direction="column"
          align="center"
          w={{ base: "100px", md: "180px" }}
          bg={index2 ? "#046EB0" : "#272728"}
          borderRadius="6px"
          py="6px"
          mx="10px"
        >
          <Text fontSize="8px">Point</Text>
          <Text fontSize="16px">03</Text>
          <Text fontSize="18px">檢測報告</Text>
        </Flex>
      </Flex>

      <Swiper
        spaceBetween={50}
        slidesPerView={1}
        onSlideChange={
          (swiper) => handleSwipe(swiper.activeIndex)
        }
        onSwiper={(swiper) => {
          // console.log(swiper, swiper.activeIndex);
          setMySwiper(swiper);
        }}
      >
        <SwiperSlide>
          <Center flexDirection="column">
            <Text
              fontWeight="200"
              textAlign="center"
              fontSize="lg"
              w={{ base: "280px", md: "480px" }}
              mb="12px"
            >
              利用身體正面及側面站姿，評估肌肉對稱性及緊繃程度，並了解身體平衡狀態
            </Text>
            <Box
              bgImage={`url('${static01}')`}
              bgRepeat="no-repeat"
              bgPosition="center"
              bgSize="cover"
              height={{ base: "160px", md: "300px" }}
              w={{ base: "320px", md: "600px" }}
              borderRadius="md"
            >
            </Box>
          </Center>
        </SwiperSlide>
        <SwiperSlide>
          <Center flexDirection="column">
            <Text
              fontWeight="200"
              textAlign="center"
              fontSize="lg"
              w={{ base: "280px", md: "480px" }}
              mb="12px"
            >
              利用深蹲評估動作，分析一連串身體動作行為，找出因為身體使用不當或代償作用造成的問題
            </Text>
            <Box
              bgRepeat="no-repeat"
              bgPosition="center"
              bgSize="cover"
              borderRadius="md"
              animation={animation}
              height={{ base: "160px", md: "300px" }}
              w={{ base: "320px", md: "600px" }}
            >
            </Box>
          </Center>
        </SwiperSlide>
        <SwiperSlide>
          <Center flexDirection="column">
            <Text
              fontWeight="200"
              textAlign="center"
              fontSize="lg"
              w={{ base: "280px", md: "480px" }}
              mb="12px"
            >
              藉由靜態及動態評估的結果，提供身體各部位完整身體風險數據，提供最合適的建議
            </Text>
            <Box
              // bgImage={`url('${feature01}')`}
              bgRepeat="no-repeat"
              bgPosition="center"
              bgSize="cover"
              borderRadius="md"
              bgImage={`url('${feature01}')`}
              animation={reportAnimation}
              height={{ base: "160px", md: "300px" }}
              w={{ base: "320px", md: "600px" }}
            >
            </Box>
          </Center>
        </SwiperSlide>
      </Swiper>
    </Box >
  )
}


export default FeatureSection;