import {
  Box,
  Flex,
  Text,
  Heading,
  AspectRatio,
  Image,
  Divider,
} from '@chakra-ui/react';

import Footer from './components/Footer';

import caseHero from './assets/images/caseHero.png';
import runbase from './assets/images/runbase.svg';
import rizap from './assets/images/rizap.svg';
import landseed from './assets/images/landseed.svg';
import pulsegym from './assets/images/pulsegym.svg';
import caseRunbase from './assets/images/caseRunbase.jpg';
import caseRizap from './assets/images/caseRizap.jpg';
import caseLandseed from './assets/images/caseLandseed.jpg';
import casePulseGym from './assets/images/casePulseGym.jpg';

const twoW = { base: "100%", sm: "100%", md: "100%", lg: "50%", xl: "50%", "2xl": "50%" }
const caselist = [
  {
    logo: runbase,
    img: caseRunbase,
    solgan: '新北訓練馬拉松跑者中心據點',
    title: '比起等到受傷時才治療，事',
    content: '森林跑站板橋館，針對運動族群需求，引進全台第一台 Crosspoint 運動風險評估檢測儀，只要透過一個指定的深蹲動作，就能透過專業機器掃描，從頭到腳評估你的身體素質，並有專人協助解讀報告，'
  },
  {
    logo: rizap,
    img: caseRizap,
    solgan: '日系首席健身房 專注在科技和驗證',
    title: '本連鎖健身中心 RIZAP',
    content: '已經在知名日本連鎖健身中心 RIZAP 啟用一段時間了!讓我們看看使用者實際體驗的感想吧，大家趕緊到 RIZAP 體驗看看喔'
  },
  {
    logo: landseed,
    img: caseLandseed,
    solgan: '運動訓練、身體檢測',
    title: '本連鎖健身中心 RIZAP',
    content: '已經在知名日本連鎖健身中心 RIZAP 啟用一段時間了!讓我們看看使用者實際體驗的感想吧，大家趕緊到 RIZAP 體驗看看喔'
  },
  {
    logo: pulsegym,
    img: casePulseGym,
    solgan: 'DREAM Fitness',
    title: 'DREAM Fitness',
    content: '已經在知名日本連鎖健身中心 RIZAP 啟用一段時間了!讓我們看看使用者實際體驗的感想吧，大家趕緊到 RIZAP 體驗看看喔'
  },
]


const Case = () => {


  return (
    <Box>
      <Flex
        h={{ base: "360px", md: "480px" }}
        w="100%"
        justify="center"
        align="center"
        bgImage={`url('${caseHero}')`}
        bgAttachment="fixed"
        direction="column"
        bgPosition="center"
        bgSize="cover"
      >
        <Heading size="lg" letterSpacing="2px"> Crosspoint 使用案例</Heading>
        <Heading my="5px" size="md" letterSpacing="2px"> 適合各種運動場域 </Heading>
      </Flex>

      <Flex align="center" justify="center" my="3em" direction="column">
        {
          caselist.map((customer, index) =>
          (
            <Flex
              key={index}
              align="center"
              direction="column"
              textAlign="center"
              my="2em"
              w={{ base: "100%", sm: "100%", md: "80%", lg: "75%", xl: "1080px" }}
            >
              <Image w="240px" src={customer.logo}></Image>
              <Heading my="1em" size="md">{customer.solgan}</Heading>
              <Flex
                justify="center"
                alignItems="center"
                wrap="wrap"
                w="100%"
              >
                <Box w={twoW} px="20px" py="32px">
                  <AspectRatio ratio={5 / 3} flex="1 1 auto">
                    <Image
                      w="260px"
                      h="60px"
                      src={customer.img}
                      alt="crosspoint"
                      borderRadius="12px"
                    />
                  </AspectRatio>
                </Box>
                <Flex textAlign="left" w={twoW} px="2%" flexDirection="column">
                  <Text fontSize="xl" letterSpacing="2px" px="12px" py="20px">{customer.title}</Text>
                  <Text
                    fontSize="md"
                    lineHeight={["tall", "taller"]}
                    letterSpacing="1.5px"
                    color="gray.400"
                    px="12px"
                  >
                    {customer.content}
                  </Text>
                </Flex>
              </Flex>
              <Divider mt="20px" />
            </Flex>
          )
          )
        }
      </Flex>
      <Footer />
    </Box >
  )
}

export default Case;

