import React from 'react'
import { NavLink as ReachLink } from 'react-router-dom'
import {
  Link,
  LinkBox,
  Text,
  Box,
  Button,
  Stack,
  Image,
  Flex,
  Icon,
  chakra,
  Divider,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  MenuItemOption,
  MenuGroup,
  MenuOptionGroup,
  MenuIcon,
  MenuCommand,
  MenuDivider,
} from '@chakra-ui/react';

import { ChevronDownIcon } from '@chakra-ui/icons';
import logo from '../assets/images/logo_w.svg';
import { MdMenu, MdClose } from "react-icons/md";


const NavLink = chakra(Link, {
  baseStyle: {
    lineHeight: "40px",
    paddingLeft: "20px",
    _hover: {
      textDecoration: "none",
      color: "blue.100",
    },
  },
  activeStyle: {
    fontWeight: "bold",
    color: "red"
  },
})

const actvieStyle = {
  // color: "blue.500",
  color: "#22CAFF",
  _active: {
    color: "blue.500",
  },
}

const MIcon = () => (
  <Icon as={MdMenu} w="24px" h="24px" />
);

const CloseIcon = () => (
  <Icon as={MdClose} w="24px" h="24px" />
);

const MenuToggle = ({ toggle, isOpen }) => {
  return (
    <Flex
      display={{ base: "flex", md: "none" }}
      onClick={toggle}
    >
      {isOpen ? <CloseIcon /> : <MIcon />}
    </Flex>
  );
};

const MItem = ({ children, to = "/", onClick, toggle, ...rest }) => {
  return (
    <NavLink
      as={ReachLink}
      to={to}
      activeStyle={actvieStyle}
      onClick={onClick}
      exact >
      <Text display="block" {...rest}>
        {children}
      </Text>
    </NavLink>
  );
};


const Header = () => {

  const [isOpen, setIsOpen] = React.useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (

    <Box sx={{ position: 'sticky', top: '0', }} zIndex="20" bg="gray.800" align="center" >
      <header>
        <Flex justify="center" py="12px">
          <Flex
            justify="space-between"
            w={{ base: "100%", sm: "100%", md: "80%", lg: "75%", xl: "1080px" }}
            mx="12px"
            align="center"
          >

            <Box
              w={{ base: "150px", md: "200px" }}
            >
              <LinkBox as={ReachLink} to="/home" exact >
                <Image objectFit="cover" src={logo} alt="crospoint" />
              </LinkBox>
            </Box>

            <Box h="24px" w="24px">
              <MenuToggle toggle={toggle} isOpen={isOpen} />
            </Box>

            <Box display={{ base: "none", md: "block" }}>
              <Box
                display={{ base: isOpen ? "block" : "none", md: "block" }}
                flexBasis={{ base: "100%", md: "auto" }}
              >
                <Stack
                  spacing={8}
                  align="center"
                  justify={["center", "space-between", "flex-end", "flex-end"]}
                  direction={["column", "row", "row", "row"]}
                  pt={[4, 4, 0, 0]}
                >
                  <MItem to="/home" onClick={toggle}>Home</MItem>
                  <MItem to="/features" onClick={toggle}> 功能介紹 </MItem>
                  <MItem to="/gym" onClick={toggle}> 解決方案 </MItem>
                  <MItem to="/case" onClick={toggle}> 成功案例 </MItem>
                  <Menu>
                    <MenuButton as={Button} rightIcon={<ChevronDownIcon />}>
                      教學支援
              </MenuButton>
                    <MenuList>
                      <MenuItem>
                        <MItem to="/faq" onClick={toggle}>常見問題</MItem>
                      </MenuItem>
                      <MenuItem>
                        <MItem to="/tutorials" onClick={toggle}>快速上手</MItem>
                      </MenuItem>
                    </MenuList>
                  </Menu>
                  <Link href="https://crosspoint-fit.web.app/" isExternal>
                    <Button
                      size="sm"
                      rounded="md"
                      _hover={{
                        bg: ["primary.100", "primary.100", "primary.600", "primary.600"]
                      }}
                    >
                      登入
                    </Button>
                  </Link>
                </Stack>
              </Box>
            </Box>

          </Flex>
        </Flex>

        <Box
          display={{ base: isOpen ? "block" : "none", md: "none" }}
          flexBasis={{ base: "100%", md: "auto" }}
          py="32px"
        >
          <Stack
            spacing={8}
            align="center"
            justify={["center", "space-between", "flex-end", "flex-end"]}
            direction={["column", "row", "row", "row"]}
            pt={[4, 4, 0, 0]}
          >
            <MItem to="/home" onClick={toggle}>Home</MItem>
            <MItem to="/features" onClick={toggle}> 功能介紹 </MItem>
            <MItem to="/gym" onClick={toggle}> 解決方案 </MItem>
            <MItem to="/case" onClick={toggle}> 成功案例 </MItem>
            <Link href="https://crosspoint-fit.web.app/" isExternal onClick={toggle}>
              <Button
                size="sm"
                rounded="md"
                _hover={{
                  bg: ["primary.100", "primary.100", "primary.600", "primary.600"]
                }}
              >
                登入
                    </Button>
            </Link>
          </Stack>
        </Box>
        <Divider />
      </header>
    </Box >
  )
};

export default Header;
