import {
  Box,
  Flex,
  Text,
  Icon,
  Divider,
} from '@chakra-ui/react';

import { RiFacebookCircleFill } from "react-icons/ri";

const Footer = () => (
  <footer>
    <Flex
      w="100%"
      bgRepeat="no-repeat"
      bgPosition="center"
      backgroundSize="cover"
      justify="center"
      align="center"
      direction="column"
      pt="40px"
    >
      <Divider my="12px" />
      <Flex
        width={{ base: "100%", sm: "100%", md: "80%", lg: "75%", xl: "1080px" }}
        justify="space-around"
        direction={{ base: "column", md: "row" }}
        align="center"
      >
        <Box>
          <Flex>
            <Icon as={RiFacebookCircleFill} w={6} h={6} color="fb.500" />
            <Text >Crosspoint</Text>
          </Flex>
        </Box>
        <Box>
          <Text fontSize="md" color="gray.400">大安區基隆路二段260號2樓</Text>
        </Box>
        <Box>
          <Text fontSize="md" color="gray.400">2021 Crosspoint .,co. All rights reserved.</Text>
        </Box>
      </Flex>
      <Divider my="12px" />
    </Flex>
  </footer>
);

export default Footer;
