import {
  Text,
  Image,
  Box,
  Center,
  Badge,
  Heading,
  Grid,
  GridItem,
  Link,
  Button,
} from '@chakra-ui/react';

import cmom from '../assets/images/news_cmom.jpg';
import newtaipei from '../assets/images/news_newtaipei.jpg';

const newsList = [
  {
    image: cmom,
    badge: 'Youtube',
    badgeColor: 'red',
    media: 'C媽咪的健身日記',
    title: '開箱AI運動風險評估檢測儀器 - 他真的能避免健身運動傷害嗎？',
    content: ' Crosspoint AI 運動風險檢測儀器，可以精準測量身體每一個部位的歪斜，讓教練能夠針對身體狀況制定訓練菜單，避免造成不必要的運動傷害，今天就來開箱，順便考考教練的專業！',
    href: 'https://www.youtube.com/watch?v=bG8USKVW0os'
  },
  {
    image: newtaipei,
    badge: 'News',
    badgeColor: 'teal',
    media: '新北市政府',
    title: '新北第1家新北運動聚點 訓練馬拉松跑者健康跑',
    content: '全台第一運動風險檢測系統，讓民眾在檢測後由教練開出建議運動處方，再搭配適合的訓練課程。',
    href: 'https://www.youtube.com/watch?v=bG8USKVW0os'
  },
  {
    image: newtaipei,
    badge: 'News',
    badgeColor: 'teal',
    media: '新北市政府',
    title: '新北第1家新北運動聚點 訓練馬拉松跑者健康跑',
    content: '全台第一運動風險檢測系統，讓民眾在檢測後由教練開出建議運動處方，再搭配適合的訓練課程。',
    href: false
  },
]

const News = () => (
  <Center>
    <Box>
      <Heading my="5" textAlign="center" letterSpacing="wide">Crosspoint 最新消息</Heading>
      <Center>
        <Box width={{ base: "100%", sm: "100%", md: "80%", lg: "75%", xl: "1080px" }}>
          <Grid templateColumns={["repeat(4, 1fr)", "repeat(4, 1fr)", "repeat(8, 1fr)", "repeat(12, 1fr)"]} gap={10} my={6} px="1em">
            {
              newsList.map((post, index) =>
              (
                <GridItem maxW="sm" borderWidth="1px" borderRadius="lg" colSpan={4} key={index}>
                  <Image height="180px" src={post.image} alt="crosspoint" />
                  <Box p="6" pb="2">
                    <Box d="flex" alignItems="baseline">
                      <Badge borderRadius="full" px="2" colorScheme={post.badgeColor}>
                        {post.badge}
                      </Badge>
                      <Box
                        color="gray.500"
                        fontWeight="semibold"
                        letterSpacing="wide"
                        fontSize="xs"
                        textTransform="uppercase"
                        ml="2"
                      >
                        {post.media}
                      </Box>
                    </Box>

                    <Box
                      mt="1"
                      fontWeight="semibold"
                      as="h4"
                      lineHeight="tight"
                    >
                      <Text fontSize="18px" color="gray.200" my="10px">
                        {post.title}
                      </Text>
                    </Box>

                    <Box noOfLines={2}>
                      <Text fontSize="16px" color="gray.400" my="8px">
                        {post.content}
                      </Text>
                    </Box>
                    {post.href &&
                      <Box my="8px" textAlign="right">
                        <Link
                          href={post.href}
                        >
                          <Button>Read More</Button>
                        </Link>
                      </Box>
                    }
                  </Box>
                </GridItem>
              )
              )
            }
          </Grid>
        </Box>
      </Center>
    </Box>
  </Center>
);

export default News;