import React from 'react'
import {
  Text,
  Box,
  Heading,
  Flex,
  Grid,
  GridItem,
  IconButton,
} from '@chakra-ui/react';

import { FiCoffee } from "react-icons/fi";
import { BiCameraHome, BiBody, BiBookBookmark } from "react-icons/bi";

const steps = [
  {
    order: '01',
    title: '邀請體驗',
    icon: < FiCoffee />,
    content: '聯絡洽詢後將有專人聯繫，根據需求不同的解決方案和應用',
  },
  {
    order: '02',
    title: '進行檢測',
    icon: < BiCameraHome />,
    content: '聯絡洽詢後將有專人聯繫，根據需供不同的解決方案和應用',
  },
  {
    order: '03',
    title: '講解分析',
    icon: < BiBookBookmark />,
    content: '操作教學外，相關物理治療團隊配備可以依據場館添購服務',
  },
  {
    order: '04',
    title: '成功導課',
    icon: < BiBody />,
    content: '培訓課程說明，除了基本配備可以依據場館需求添購服務',
  },
]

const CoachFlow = () => (
  <Box
    bgColor="#354A69"
    mb="2em"
    py="2em">
    <Flex justify="center">
      <Flex
        direction="column"
        justify="center"
        alignItems="center"
        w={{ base: "95%", sm: "95%", md: "80%", lg: "75%", xl: "1080px" }}
      >
        <Heading>提升導課轉換率</Heading>
        <Text>體驗課程結合體態檢測更具說服力</Text>
        <Box
          my="1.25em"
          w="100%"
          borderBottom=" 2px solid #1890FF"
          borderRadius="full"
        />
      </Flex>
    </Flex>

    <Flex justifyContent="center"
    >
      <Box width={{ base: "100%", sm: "100%", md: "80%", lg: "75%", xl: "1080px" }}>
        <Grid templateColumns={["repeat(8, 1fr)", "repeat(8, 1fr)", "repeat(8, 1fr)", "repeat(16, 1fr)"]} gap={10} my={10} px="1em">

          {
            steps.map((step, index) =>
            (
              <GridItem position="relative" w="100%" colSpan={4} key={index}>
                <Flex
                  w="100%"
                  borderRadius="8px"
                  bg="gray.800"
                  boxShadow="0px 4px 28px rgba(43, 43, 43, 0.6)"
                >
                  <Flex
                    align="center"
                    justify="center"
                    direction="column"
                    px="24px"
                    py="40px"
                  >
                    <Text fontSize="8px">STEP</Text>
                    <Text fontSize="16px" color="#fff" mb="6px">{step.order}</Text>
                    <IconButton
                      variant="outline"
                      aria-label="Call Sage"
                      fontSize="20px"
                      icon={step.icon}
                      isRound
                    />
                    <Text my="6px" fontSize="18px" color="gray.200" >{step.title}</Text>
                    <Text textAlign="center" fontSize="14px" color="gray.300" >{step.content}</Text>
                  </Flex>
                </Flex>
              </GridItem>
            )
            )
          }
        </Grid>
      </Box>
    </Flex>
  </Box>
);

export default CoachFlow;