import React from 'react';
import Typical from 'react-typical'

import {
  Box,
  Flex,
  Text,
  Image,
  keyframes,
  usePrefersReducedMotion,
} from '@chakra-ui/react';

import ImportFlow from './components/ImportFlow';
import GymBenefit from './components/GymBenefit';
import CoachFlow from './components/CoachFlow';

import gymHero from './assets/images/gymHero.png';
import pattern from './assets/images/logo_pattern.jpg';


const bgiLoop = keyframes`
  from { background-position: 0 0; }
  to { background-position: 200px -200px; }
`;


function Gym() {


  const prefersReducedMotion = usePrefersReducedMotion();
  const animation = prefersReducedMotion
    ? undefined
    : `${bgiLoop} infinite 8s linear`;


  return (
    <Box>
      <Flex
        justifyContent="center"
        my="2em"
        minH="420px"
        w="100%"
        bgImage={`url('${pattern}')`}
        bgSize="200px 200px"
        animation={animation}
      >
        <Flex justifyContent="center" alignItems="center" width={{ base: "100%", sm: "100%", md: "80%", lg: "75%", xl: "1080px" }} px="20px">
          <Box p={3} width="50%">
            <Text textAlign="left" fontSize="32px">
              打造最安全最有效率的
              <Typical
                steps={['健身房🏋️', 1200, '運動場域🤸', 1200, '健身工作室🏋️‍♀️', 1200, '物理治療中心🧘', 1500]}
                loop={Infinity}
                wrapper="p"
              />
            </Text>
          </Box>
          <Box width="50%">
            <Image src={gymHero} />
          </Box>
        </Flex>
      </Flex >
      <ImportFlow />
      <GymBenefit />
      <CoachFlow />
    </Box>
  );
}

export default Gym;
