import React from 'react';
import Typical from 'react-typical';
// import { useState, useEffect } from 'react';
import {
  AspectRatio,
  Image,
  Box,
  Flex,
  Text,
  Button,
  Grid,
  GridItem,
  Heading,
} from '@chakra-ui/react';

import Recommend from './components/Recommend';
import Footer from './components/Footer';
import News from './components/News';
import FeatureSection from './components/FeatureSection';


import heroText from './assets/images/hero_text.png';
import heroTextSm from './assets/images/hero_text_sm.png';
import hero from './assets/images/hero.png';
import demo from './assets/images/demo.png';
import gymBg from './assets/images/gym_list_bg.png';
import landseed from './assets/images/landseed.svg';
import rizap from './assets/images/rizap.svg';
import runbase from './assets/images/runbase.svg';
import pulsegym from './assets/images/pulsegym.svg';
import dreamfitness from './assets/images/dreamfitness.png';
import introMap from './assets/images/intro_map.png';

const twoW = { base: "100%", sm: "100%", md: "100%", lg: "50%", xl: "50%", "2xl": "50%" }

function Home() {
  const windowWidth = window.screen.width;
  const placeArray = ['康復', '運動', '健身'];
  return (
    <Box fontSize="xl">
      <Flex justifyContent="center" margin="0 auto" px="20px"
        width={{ base: "100%", sm: "100%", md: "80%", lg: "75%", xl: "960px", "2xl": "1280px" }}
      >
        <Grid
          templateColumns={["repeat(4, 1fr)", "repeat(4, 1fr)", "repeat(4, 1fr)", "repeat(6, 1fr)"]}
          gap={4}
          py="2em"
          className="hero"
          width="100%"
        >
          <GridItem colSpan={[4, 4, 4, 3]} >
            <Flex width="100%" height="100%" alignItems="center">
              <Box width="100%">
                <AspectRatio ratio={5 / 3} flex="1 1 auto">
                  <Box
                    bgImage={[`url('${heroTextSm}')`, `url('${heroTextSm}')`, `url('${heroTextSm}')`, `url('${heroText}')`]}
                    bgRepeat="no-repeat"
                    bgPosition="center"
                    backgroundSize="cover"
                  >
                  </Box>
                </AspectRatio>
                <Box ml={["0em", "1.5em"]} textAlign={["center", "center", "center", "left"]}>
                  <Button size="lg" mx="1">申請體驗</Button>
                  <Button size="lg" mx="1" variant="outline">登入後台</Button>
                </Box>
              </Box>
            </Flex>
          </GridItem>
          <GridItem colSpan={[4, 4, 4, 3]} colEnd={["auto", "auto", "auto", "7"]}>
            <AspectRatio ratio={5 / 5} textAlign="center" >
              <Image objectFit="cover" src={hero} alt="crosspoint" />
            </AspectRatio>
          </GridItem>
        </Grid>
      </Flex >
      <Flex justify="center" w="100%">
        <Flex justifyContent="center" my="1em" w="100%" >
          <Flex
            direction="column"
            justify="center"
            alignItems="center"
            textAlign="center"
            w={{ base: "100%", sm: "100%", md: "100%", lg: "100%", xl: "100%", "2xl": "100%" }}
            minh="300px"
            py="80px"
            bgImage={`url('${gymBg}')`}
            bgRepeat="no-repeat"
            bgPosition="center"
            backgroundSize="cover"
          >
            <Heading as="h3" mb="1.5em" size="lg">
              深受優秀
              <Typical
                steps={['健身', 3000, '運動', 3000, '康復', 3000, '跑者', 3000]}
                loop={Infinity}
                wrapper="span"
              />
              團隊信賴
            </Heading>
            <Flex
              w={{ base: "100%", sm: "100%", md: "80%", lg: "75%", xl: "900px", "2xl": "100%" }}
              justifyContent="center" flexWrap="wrap" width="100%" mt="32px">
              <Image w={["130px", "130px", "260px"]} h="60px" my="5" src={landseed} alt="crosspoint" />
              <Image w={["130px", "130px", "260px"]} h="60px" my="5" src={rizap} alt="crosspoint" />
              <Image w={["130px", "130px", "260px"]} h="60px" my="5" src={runbase} alt="crosspoint" />
              <Image w={["130px", "130px", "260px"]} h="60px" my="5" src={pulsegym} alt="crosspoint" />
              <Image w={["130px", "130px", "260px"]} h="60px" my="5" src={dreamfitness} alt="crosspoint" />
            </Flex>
          </Flex>
        </Flex>
      </Flex>
      <Flex justifyContent="center" my="2.5em" textAlign="center">
        <Box>
          <Heading my="1em" size="lg">Crosspoint AI 運動風險檢測</Heading>
          <Flex
            justify="center"
            alignItems="center"
            wrap="wrap"
            w={{ base: "100%", sm: "100%", md: "80%", lg: "75%", xl: "1080px" }}
          >
            <Box w={twoW} px="2%" py="32px">
              <AspectRatio ratio={5 / 3} flex="1 1 auto">
                <Image w="260px" h="60px" src={introMap} alt="crosspoint" />
              </AspectRatio>
            </Box>
            <Flex textAlign="left" w={twoW} px="2%" flexDirection="column">
              <Text letterSpacing="2px" px="12px" py="20px">身體表現客觀數據</Text>
              <Text
                fontSize="md"
                lineHeight={["tall", "taller"]}
                letterSpacing="1.5px"
                color="gray.400"
                px="12px"
              >
                美國國家運動醫學院-矯正運動國際證照 NASM-CES 理論，結合 FMS、SFMA 等功能性訓練評估標準分類各項人體動作狀態。
                透過數位攝影機捕捉人體關鍵部位動態變化,搭配 AI 影像分析系統,迅速判斷身體狀況。
              </Text>
            </Flex>
          </Flex>
        </Box>
      </Flex>
      <Flex justifyContent="center" my="2em" textAlign="center">
        <Box>
          <Heading as="h5" my="1em" size="lg">為什麼選擇 Crosspoint？</Heading>
          <Flex
            justify="center"
            alignItems="center"
            wrap="wrap"
            w={{ base: "100%", sm: "100%", md: "80%", lg: "75%", xl: "1080px" }}
          >
            <Flex textAlign="left" w={twoW} px="2%" direction="column">
              <Text letterSpacing="2px" px="12px" py="2px">降低運動風險</Text>
              <Text letterSpacing="2px" px="12px" pb="20px">運動更安全更有效率</Text>
              <Text
                fontSize="md"
                lineHeight={["tall", "taller"]}
                letterSpacing="1.5px"
                color="gray.400"
                px="12px"
              >
                美國國家運動醫學院-矯正運動專家國際證照NASM-CES 的理論，結合國際 FMS、SFMA 等功能性訓練評估標準來分類各項人體動作狀態。
                透過數位攝影機捕捉人體關鍵部位動態變化,搭配 AI 影像分析系統,迅速判斷身體狀況。
              </Text>
            </Flex>
            <Box w={twoW} px="2%" my="20px">
              <AspectRatio ratio={5 / 3} flex="1 1 auto">
                <Image w="260px" h="60px" src={demo} alt="crosspoint" />
              </AspectRatio>
            </Box>
          </Flex>
        </Box>
      </Flex>
      <Recommend />
      <FeatureSection />
      <News />
      <Footer />
    </Box >
  );
}

export default Home;
