
import {
  Box,
  Flex,
  Text,
  Heading,
  Divider,
  Image,
  Grid,
  GridItem,
  Accordion,
  AccordionItem,
  AccordionButton,
  AccordionPanel,
  AccordionIcon,
} from '@chakra-ui/react';

import Footer from './components/Footer';
import t01 from './assets/images/tutorials_workspace.jpg';

function Tutorials() {

  return (
    <Flex justify="center" my="12">
      <Flex
        w={{ base: "95%", sm: "95%", md: "80%", lg: "75%", xl: "1080px" }}
        justify="center"
        align="center"
        bgAttachment="fixed"
        direction="column"
        bgPosition="center"
        bgSize="cover"
      >
        <Heading size="lg" letterSpacing="2px"> Getting Started 快速上手</Heading>
        <Heading my="5px" size="md" letterSpacing="2px"> 使用 Crosspoint 的介紹 </Heading>
        <Flex w="100%" my="50px" px="24px">
          <Box w="25%">
            <Text>Getting Starting</Text>
            <Text>基本設定</Text>
            <Text>建立會員</Text>
          </Box>
          <Box w="75%" px="32px">
            <Text fontSize="18px">
              嗨，歡迎來到Crosspoint 平台！我們非常感謝您運用。<br />
              以下是您在Crosspoint 平台的檢測旅程中，會經歷的幾個階段：
            </Text>
            <Box
              background="blue.700"
              w="160px"
              textAlign="center"
              py="8px"
              borderRadius="20px"
              my="12px"
            >
              Chaptor01
            </Box>
            <Text fontSize="18px" my="2px"> 場館管理 </Text>
            <Text fontSize="16px" color="gray.200" my="2px">
              請確認在縮放與版面配置 縮放設定 100% <br />
            依次進入「設定-系統-顯示器」，在右側的「縮放與版面配置」
            </Text>
            <Image my="20px" borderRadius="5px" src={t01} alt="workspace" />
            <Text fontSize="18px" my="2px"> 新增會員 </Text>
            <Text fontSize="16px" color="gray.200" my="2px">
              請確認在縮放與版面配置 縮放設定 100% <br />
            依次進入「設定-系統-顯示器」，在右側的「縮放與版面配置」
            </Text>
            <Image my="20px" borderRadius="5px" src={t01} alt="workspace" />
            <Divider />
          </Box>
        </Flex>
      </Flex>

      {/* <Footer /> */}
    </Flex >
  );
}

export default Tutorials;

